/*------ header start ------*/
.header {
  position: relative;
  background-image: url("../images/header-back-1.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 766px;
  .container {
    position: relative;
  }
  .nav-bar {
    display: flex;
    justify-content: flex-start;
    padding: 30px 0 0 0;
    .logo {
      margin: 0 0 0 55px;
    }
    .nav-list {
      // margin: 0 auto;
      .upper {
        position: relative;
        display: flex;
        justify-content: center;
        margin: -12px 0 0 0;
        padding: 0 0 14px 0;
        &::after {
          content: "";
          width: 150px;
          height: 1px;
          background-color: rgba(255, 255, 255, 0.2);
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          bottom: 0;
        }
        .license {
          margin: 0 0 0 24px;
          font-family: t-reg;
          font-size: 19px;
          color: rgb(147, 149, 175);
          i {
            font-size: 13px;
            padding: 0 0 0 8px;
          }
        }
        .phone {
          i {
            color: rgb(147, 149, 175);
            font-size: 13px;
            padding: 0 0 0 8px;
          }
          a {
            font-family: t-reg;
            font-size: 19px;
            color: rgb(41, 145, 214);
          }
          span {
            font-family: t-reg;
            font-size: 19px;
            color: rgb(147, 149, 175);
            padding: 0;
          }
        }
      }
      ul {
        margin: 20px 0 0 0;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        li {
          font-family: ar-reg;
          font-size: 19px;
          color: #fff;
          line-height: 1.312;
          margin: 0 0 0 22px;
          cursor: pointer;
          transition: all .3s ease-in-out;
          &:hover {
            color: rgb(62, 110, 223);
          }
          a {
            font-family: inherit;
            font-size: inherit;
            color: inherit;
          }
          &.hover {
            position: relative;
            &::after {
              content: "\f107";
              font-family: "Font Awesome 5 Free";
              font-weight: 600;
              font-size: 12px;
              color: #fff;
              margin: 3px 5px 0 0;
              transition: all .3s ease-in-out;
            }
            &:hover {
              ul {
                opacity: 1;
                visibility: visible;
                top: 15px;
              }
              &::after {
                color: rgb(62, 110, 223);
              }
            }
            ul {
              opacity: 0;
              visibility: hidden;
              width: 360px;
              position: absolute;
              top: 40px;
              left: 50%;
              transform: translateX(-50%);
              z-index: 99;
              background-color: #fff;
              border-radius: 5px;
              padding: 20px 30px;
              display: flex;
              justify-content: flex-start;
              align-items: center;
              flex-direction: column;
              transition: all .5s ease-in-out;
              &::before {
                content: "";
                width: 0; 
                height: 0; 
                border-left: 7px solid transparent;
                border-right: 7px solid transparent;
                border-bottom: 7px solid #fff;
                position: absolute;
                top: -7px;
                left: 50%;
                transform: translateX(-50%);
              }
              li {
                color: #444;
                margin: 8px 0;
                transition: all .3s ease-in-out;
                &:hover {
                  color: rgb(62, 110, 223);
                }
              }
            }
          }
        }
      }
    }
    .contact-us-icon {
      position: absolute;
      top: -25px;
      left: -40px;
      .ellipse-3 {
        border-style: solid;
        border-width: 2px;
        border-color: rgba(255, 255, 255, 0.051);
        border-radius: 50%;
        width: 212px;
        height: 212px;
        display: flex;
        justify-content: center;
        align-items: center;
        .ellipse-2 {
          border-style: solid;
          border-width: 2px;
          border-color: rgba(255, 255, 255, 0.102);
          border-radius: 50%;
          width: 164px;
          height: 164px;
          display: flex;
          justify-content: center;
          align-items: center;
          .ellipse-1 {
            border-color: rgba(255, 255, 255, 0.2);
            border-style: solid;
            border-width: 2px;
            border-radius: 50%;
            width: 128px;
            height: 128px;
            display: flex;
            justify-content: center;
            align-items: center;
            .contact {
              width: 102px;
              height: 102px;
              border-radius: 50%;
              background-color: rgb(62, 110, 223);
              color: #fff;
              display: flex;
              justify-content: center;
              align-items: center;
              flex-direction: column;
              cursor: default;
              i {
                font-size: 24px;
                padding: 0 0 5px 0;
              }
              span {
                font-family: ar-bold;
                font-size: 17px;
              }
            }
          }
        }
      }
    }
  }
  .header-inner {
    margin: 112px 0 0 0;
    .inner-text {
      padding: 45px 0 0 0;
      p {
        margin: 0;
        padding: 0;
        font-family: ar-reg;
        font-size: 30px;
        color: rgb(255, 255, 255);
        line-height: 1.6;
      }
    }
    .inner-btns {
      margin: 33px 0 0 0;
      button {
        width: 195px;
        height: 48px;
        border-radius: 50px;
        border: none;
        outline: none;
        font-family: ar-bold;
        font-size: 17px;
        color: rgb(255, 255, 255);
        transition: all .5s ease-in-out;
        &:nth-of-type(1) {
          background-color: rgb(62, 110, 223);
          border: 2px solid rgb(62, 110, 223);
          margin: 0 0 0 8px;
          &:hover {
            // background-color: transparent;
            // border-style: solid;
            // border-width: 2px;
            // border-color: rgba(255, 255, 255, 0.2);
            transform: scale(0.950);
          }
        }
        &:nth-of-type(2) {
          background-color: transparent;
          border-style: solid;
          border-width: 2px;
          border-color: rgba(255, 255, 255, 0.2);
          &:hover {
            // background-color: rgb(62, 110, 223);
            // border: 2px solid rgb(62, 110, 223);
            transform: scale(0.950);
          }
        }
      }
    }
    .inner-vid {
      position: relative;
      img {
        left: 50%;transition: all .5s ease-in-out;
        &:nth-of-type(2) {
          position: absolute;
          top: 50%;
          transform: translate(-50%, -50%);
        }
      }
      &:hover {
        img {
          &:nth-of-type(2) {
            transform: translate(-50%, -50%) scale(0.9);
          }
        }
      }
    }
  }
  .fixed-icons {
    position: absolute;
    right: 30px;
    bottom: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 999;
    &.fixed {
      position: fixed;
    }
    a {
      width: 70px;
      height: 70px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
    }
    .phone {
      background-color: rgb(219, 79, 79);
      margin: 0 0 9px 0;
      i {
        font-size: 39px;
      }
    }
    .whats-app {
      background-color: rgb(13, 193, 67);
      i {
        font-size: 46px;
      }
    }
  }
  .scroll-icon {
    position: absolute;
    bottom: 28px;
    left: 50%;
    transform: translateX(-50%);
    transition: all .5s ease-in-out;
    &:hover {
      animation-name: scroll-icon;
      animation-duration: .5s;
      animation-direction: alternate;
      animation-iteration-count: infinite;
    }
  }
}
@keyframes scroll-icon {
  0% {
    transform: translate(-50%, 0%);
  }
  100% {
    transform: translate(-50%, 10%);
  }
}
/*------ header end ------*/



