        height: 164px;
        border-style: solid;
        border-width: 2px;
        border-color: rgba(255, 255, 255, 0.102);
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-in-out;
