      padding: 0;
    }
    .fst-list {
      ul {
        li {
          margin: 0 0 20px 0;
          font-family: ar-reg;
          font-size: 16px;
          color: rgb(254, 254, 254);
          i {
            width: 18px;
            height: 18px;
            text-align: center;
            margin: 0 0 0 20px;
            font-size: 16px;
            color: rgb(147, 149, 175);
          }
          span {
            font-family: t-reg;
            font-size: 16px;
            color: #9395af;
          }
        }
      }
    }
    .sec-list {
      display: flex;
      ul {
        margin: 0 0 0 50px;
        &:last-child {
          margin: 0;
        }
        li {
          margin: 0 0 20px 0;
          a {
            font-family: ar-reg;
            font-size: 16px;
            color: rgb(147, 149, 175);
            transition: all .3s ease-in-out;
            &:hover {
              color: #fff;
            }
          }
        }
      }
    }
    .th-list {
      ul {
        li {
          display: flex;
          margin: 0 0 20px 0;
          font-family: ar-reg;
          font-size: 16px;
          color: rgb(254, 254, 254);
          i {
            width: 18px;
            height: 18px;
            text-align: center;
            margin: 0 0 0 20px;
            font-size: 16px;
            color: rgb(254, 254, 254);
          }
          p {
            margin: 0;
            padding: 0 0 0 130px;
          }
          a {
            font-family: t-reg;
            font-size: inherit;
            color: inherit;
            transition: all .3s ease-in-out;
            &:hover {
              color: #3e6edf;
            }
          }
          span {
            padding: 0 3px;
          }
        }
      }
    }
  }
  .icons {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 30px 0 0 0;
    .icon-container {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 0 0 10px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      font-size: 18px;
      color: #fff;
      transition: all .5s ease-in-out;
      &:hover {
        background: #3e6edf !important;
        i {
          transform: rotateY(360deg);
        }
      }
      i {
        transition: all .5s ease-in-out;
      }
      &:last-child {
        margin: 0;
      }
      &:nth-of-type(1) {
        background-color: rgb(255, 225, 0);
      }
      &:nth-of-type(2) {
        background-color: rgb(232, 63, 58);
      }
      &:nth-of-type(3) {
        background: radial-gradient(circle at 30% 107%, #ef7d2f 0%, #fd5949 30%,#d6249f 60%,#285AEB 90%);
        background-size: 125%;
      }
      &:nth-of-type(4) {
        background-color: rgb(6, 120, 182);
      }
      &:nth-of-type(5) {
        background-color: rgb(55, 177, 225);
      }
      &:nth-of-type(6) {
        background-color: rgb(75, 105, 176);
      }
    }
  }
  .copyrights {
    margin: 50px 0 0 0;
    padding: 12px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid rgba(255, 255, 255, 0.102);
    span {
      font-family: ar-reg;
      font-size: 14px;
      color: #9395af;
      img {
        margin: 0 5px;
      }
    }
  }
}
