﻿html{
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: #DDD;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: rgb(62, 110, 223);
    }

    scroll-behavior: smooth;

    overflow-x: hidden;

    scrollbar-width: thin;
    scrollbar-color: rgba(62, 110, 223, 0.8) rgba(#DDD,1);
    // scroll-behavior: smooth;
}

body{
    font-family: 'reg';
    overflow-y: hidden;
}



